<template>
  <view class="canvas-record-page">
    <!-- Áä∂ÊÄÅÊòæÁ§∫ -->
    <view class="status-section">
      <text class="status-text">{{ statusText }}</text>
      <text class="time-text" v-if="isRecording || hasRecord">{{ formattedTime }}</text>
    </view>

    <!-- CanvasÊ≥¢ÂΩ¢Âõæ -->
    <view class="canvas-container">
      <canvas 
        canvas-id="waveformCanvas" 
        id="waveformCanvas"
        class="waveform-canvas"
        :style="{ width: canvasWidth + 'px', height: canvasHeight + 'px' }"
      ></canvas>
    </view>

    <!-- ÊéßÂà∂ÊåâÈíÆ -->
    <view class="control-section">
      <!-- ÂºÄÂßãÂΩïÈü≥ -->
      <button
        class="control-btn start-btn"
        v-if="!isRecording && !hasRecord"
        @tap="handleStartRecord"
        :loading="isStarting"
      >
        ÂºÄÂßãÂΩïÈü≥
      </button>

      <!-- ÂÅúÊ≠¢ÂΩïÈü≥ -->
      <button
        class="control-btn stop-btn"
        v-if="isRecording"
        @tap="handleStopRecord"
      >
        ÂÅúÊ≠¢ÂΩïÈü≥
      </button>

      <!-- ÈáçÊñ∞ÂΩïÈü≥ -->
      <button
        class="control-btn redo-btn"
        v-if="!isRecording && hasRecord"
        @tap="handleRedoRecord"
      >
        ÈáçÊñ∞ÂΩïÈü≥
      </button>

      <!-- Êí≠ÊîæÂΩïÈü≥ -->
      <button
        class="control-btn play-btn"
        v-if="hasRecord && !isRecording"
        @tap="handlePlay"
      >
        {{ isPlaying ? 'ÊöÇÂÅúÊí≠Êîæ' : 'Êí≠ÊîæÂΩïÈü≥' }}
      </button>
    </view>

    <!-- Ë∞ÉËØï‰ø°ÊÅØ -->
    <view class="debug-info" v-if="isDev">
      <text>Êñá‰ª∂Ë∑ØÂæÑ: {{ filePath || 'Êó†' }}</text>
      <text>ÂΩïÈü≥Êó∂Èïø: {{ recordTime }}Áßí</text>
      <text>Èü≥Èáè: {{ currentVolume.toFixed(1) }}</text>
      <text>Ê≥¢ÂΩ¢ÁÇπÊï∞: {{ waveformPoints.length }}</text>
    </view>
  </view>
</template>

<script>
import { RecordManager } from '../../utils/record-manager.js'

export default {
  data() {
    return {
      isRecording: false,      // ÊòØÂê¶Ê≠£Âú®ÂΩïÈü≥
      hasRecord: false,        // ÊòØÂê¶ÊúâÂΩïÈü≥Êñá‰ª∂
      isPlaying: false,        // ÊòØÂê¶Ê≠£Âú®Êí≠Êîæ
      isStarting: false,       // ÂºÄÂßãÂΩïÈü≥‰∏≠
      recordTime: 0,           // ÂΩïÈü≥Êó∂Èïø
      filePath: '',            // Êñá‰ª∂Ë∑ØÂæÑ
      isDev: true,             // ÂºÄÂèëÊ®°Âºè
      recordManager: null,     // ÂΩïÈü≥ÁÆ°ÁêÜÂô®
      innerAudioContext: null, // Èü≥È¢ëÊí≠Êîæ‰∏ä‰∏ãÊñá
      
      // CanvasÁõ∏ÂÖ≥
      canvasWidth: 0,          // CanvasÂÆΩÂ∫¶
      canvasHeight: 200,       // CanvasÈ´òÂ∫¶
      canvasContext: null,     // Canvas‰∏ä‰∏ãÊñá
      waveformPoints: [],      // Ê≥¢ÂΩ¢Êï∞ÊçÆÁÇπ
      maxPoints: 200,          // ÊúÄÂ§ßÊ≥¢ÂΩ¢ÁÇπÊï∞
      currentVolume: 0,        // ÂΩìÂâçÈü≥Èáè
      
      // Âä®ÁîªÁõ∏ÂÖ≥
      animationFrame: null,    // Âä®ÁîªÂ∏ßID
      lastDrawTime: 0,         // ‰∏äÊ¨°ÁªòÂà∂Êó∂Èó¥
      drawInterval: 50          // ÁªòÂà∂Èó¥ÈöîÔºàmsÔºâÔºåÊéßÂà∂ÁªòÂà∂È¢ëÁéá
    }
  },

  computed: {
    // Áä∂ÊÄÅÊñáÊú¨
    statusText() {
      if (this.isRecording) return 'üé§ ÂΩïÈü≥‰∏≠...'
      if (this.hasRecord) return '‚úÖ ÂΩïÈü≥ÂÆåÊàê'
      return 'üì± ÂáÜÂ§áÂΩïÈü≥'
    },

    // Ê†ºÂºèÂåñÊó∂Èó¥
    formattedTime() {
      const minutes = Math.floor(this.recordTime / 60)
      const seconds = this.recordTime % 60
      return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
    }
  },

  onLoad() {
    console.log('CanvasÂΩïÈü≥È°µÈù¢Âä†ËΩΩ')
    
    // ÂàùÂßãÂåñÂΩïÈü≥ÁÆ°ÁêÜÂô®
    this.recordManager = new RecordManager('canvas_record')
    this.recordManager.init()
    
    // ËÆæÁΩÆÊó∂Èó¥Êõ¥Êñ∞ÂõûË∞É
    this.recordManager.setTimeUpdateCallback((seconds, formatted) => {
      if (this.isRecording || this.recordManager.isRecording) {
        this.recordTime = seconds
      }
    })

    // ËÆæÁΩÆÈü≥ÈáèÊõ¥Êñ∞ÂõûË∞É
    this.recordManager.setVolumeUpdateCallback((volume) => {
      this.currentVolume = volume
      this.addWaveformPoint(volume)
    })

    // ÂàùÂßãÂåñCanvas
    this.initCanvas()
    
    // ÂàùÂßãÂåñÈü≥È¢ëÊí≠Êîæ‰∏ä‰∏ãÊñá
    this.initInnerAudioContext()
  },

  onUnload() {
    // Ê∏ÖÁêÜËµÑÊ∫ê
    if (this.recordManager) {
      this.recordManager.destroy()
    }
    
    // ÈîÄÊØÅÈü≥È¢ë‰∏ä‰∏ãÊñá
    if (this.innerAudioContext) {
      this.innerAudioContext.destroy()
    }
    
    // ÂÅúÊ≠¢Âä®Áîª
    if (this.animationFrame) {
      cancelAnimationFrame(this.animationFrame)
    }
  },

  methods: {
    // ÂàùÂßãÂåñCanvas
    initCanvas() {
      // ‰ΩøÁî®nextTickÁ°Æ‰øùDOMÂ∑≤Ê∏≤Êüì
      this.$nextTick(() => {
        // Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØÔºåËÆ°ÁÆóCanvasÂÆΩÂ∫¶
        const systemInfo = uni.getSystemInfoSync()
        this.canvasWidth = systemInfo.windowWidth - 60 // ÂáèÂéªÂ∑¶Âè≥ËæπË∑ù
        
        // Ëé∑ÂèñCanvas‰∏ä‰∏ãÊñá
        this.canvasContext = uni.createCanvasContext('waveformCanvas', this)
        
        // ÂàùÂßãÂåñÊ≥¢ÂΩ¢Êï∞ÊçÆ
        this.waveformPoints = []
        
        // ÁªòÂà∂ÂàùÂßãËÉåÊôØ
        this.drawBackground()
      })
    },

    // ÁªòÂà∂ËÉåÊôØ
    drawBackground() {
      const ctx = this.canvasContext
      if (!ctx) return

      // Ê∏ÖÁ©∫ÁîªÂ∏É
      ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight)
      
      // ÁªòÂà∂ËÉåÊôØ
      ctx.setFillStyle('#f5f5f5')
      ctx.fillRect(0, 0, this.canvasWidth, this.canvasHeight)
      
      // ÁªòÂà∂‰∏≠ÂøÉÁ∫ø
      ctx.setStrokeStyle('#e0e0e0')
      ctx.setLineWidth(1)
      ctx.beginPath()
      ctx.moveTo(0, this.canvasHeight / 2)
      ctx.lineTo(this.canvasWidth, this.canvasHeight / 2)
      ctx.stroke()
      
      ctx.draw()
    },

    // Ê∑ªÂä†Ê≥¢ÂΩ¢ÁÇπ
    addWaveformPoint(volume) {
      // Â∞ÜÈü≥ÈáèËΩ¨Êç¢‰∏∫Ê≥¢ÂΩ¢È´òÂ∫¶Ôºà0-100 -> 0-canvasHeight/2Ôºâ
      const maxHeight = this.canvasHeight / 2
      const height = (volume / 100) * maxHeight
      
      // Ê∑ªÂä†Êñ∞ÁÇπ
      this.waveformPoints.push({
        volume: volume,
        height: height,
        timestamp: Date.now()
      })
      
      // ÈôêÂà∂ÁÇπÊï∞Ôºå‰øùÊåÅÊªöÂä®ÊïàÊûú
      if (this.waveformPoints.length > this.maxPoints) {
        this.waveformPoints.shift()
      }
      
      // ‰ΩøÁî®ËäÇÊµÅÊéßÂà∂ÁªòÂà∂È¢ëÁéáÔºåÈÅøÂÖçËøáÂ∫¶ÁªòÂà∂
      const now = Date.now()
      if (now - this.lastDrawTime >= this.drawInterval) {
        this.drawWaveform()
        this.lastDrawTime = now
      }
    },

    // ÁªòÂà∂Ê≥¢ÂΩ¢
    drawWaveform() {
      const ctx = this.canvasContext
      if (!ctx || this.waveformPoints.length === 0) return

      // Ê∏ÖÁ©∫ÁîªÂ∏É
      ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight)
      
      // ÁªòÂà∂ËÉåÊôØ
      ctx.setFillStyle('#f5f5f5')
      ctx.fillRect(0, 0, this.canvasWidth, this.canvasHeight)
      
      // ÁªòÂà∂‰∏≠ÂøÉÁ∫ø
      ctx.setStrokeStyle('#e0e0e0')
      ctx.setLineWidth(1)
      ctx.beginPath()
      ctx.moveTo(0, this.canvasHeight / 2)
      ctx.lineTo(this.canvasWidth, this.canvasHeight / 2)
      ctx.stroke()
      
      // ËÆ°ÁÆóÊØè‰∏™ÁÇπÁöÑXÂùêÊ†áÈó¥Èöî
      const pointSpacing = this.canvasWidth / this.maxPoints
      const centerY = this.canvasHeight / 2
      
      // ÁªòÂà∂‰∏äÂçäÈÉ®ÂàÜÊ≥¢ÂΩ¢ÔºàÁªøËâ≤Ôºâ
      ctx.setStrokeStyle('#07c160')
      ctx.setLineWidth(2)
      ctx.beginPath()
      
      for (let i = 0; i < this.waveformPoints.length; i++) {
        const x = i * pointSpacing
        const y = centerY - this.waveformPoints[i].height
        
        if (i === 0) {
          ctx.moveTo(x, y)
        } else {
          ctx.lineTo(x, y)
        }
      }
      ctx.stroke()
      
      // ÁªòÂà∂‰∏ãÂçäÈÉ®ÂàÜÊ≥¢ÂΩ¢ÔºàÂØπÁß∞Ôºâ
      ctx.beginPath()
      for (let i = 0; i < this.waveformPoints.length; i++) {
        const x = i * pointSpacing
        const y = centerY + this.waveformPoints[i].height
        
        if (i === 0) {
          ctx.moveTo(x, y)
        } else {
          ctx.lineTo(x, y)
        }
      }
      ctx.stroke()
      
      // Â°´ÂÖÖÊ≥¢ÂΩ¢Âå∫ÂüüÔºàÊ∏êÂèòÊïàÊûúÔºâ
      if (this.waveformPoints.length > 1) {
        // ‰∏äÂçäÈÉ®ÂàÜÂ°´ÂÖÖ
        ctx.setFillStyle('rgba(7, 193, 96, 0.2)')
        ctx.beginPath()
        ctx.moveTo(0, centerY)
        for (let i = 0; i < this.waveformPoints.length; i++) {
          const x = i * pointSpacing
          const y = centerY - this.waveformPoints[i].height
          ctx.lineTo(x, y)
        }
        ctx.lineTo((this.waveformPoints.length - 1) * pointSpacing, centerY)
        ctx.closePath()
        ctx.fill()
        
        // ‰∏ãÂçäÈÉ®ÂàÜÂ°´ÂÖÖ
        ctx.beginPath()
        ctx.moveTo(0, centerY)
        for (let i = 0; i < this.waveformPoints.length; i++) {
          const x = i * pointSpacing
          const y = centerY + this.waveformPoints[i].height
          ctx.lineTo(x, y)
        }
        ctx.lineTo((this.waveformPoints.length - 1) * pointSpacing, centerY)
        ctx.closePath()
        ctx.fill()
      }
      
      // ÁªòÂà∂ÂΩìÂâçÈü≥ÈáèÊåáÁ§∫Á∫ø
      if (this.isRecording && this.waveformPoints.length > 0) {
        const lastIndex = this.waveformPoints.length - 1
        const x = lastIndex * pointSpacing
        
        ctx.setStrokeStyle('#ff4444')
        ctx.setLineWidth(2)
        ctx.beginPath()
        ctx.moveTo(x, 0)
        ctx.lineTo(x, this.canvasHeight)
        ctx.stroke()
      }
      
      // Êèê‰∫§ÁªòÂà∂
      ctx.draw()
    },

    // ÂàùÂßãÂåñÈü≥È¢ëÊí≠Êîæ‰∏ä‰∏ãÊñá
    initInnerAudioContext() {
      this.innerAudioContext = uni.createInnerAudioContext()
      this.innerAudioContext.autoplay = false
      this.innerAudioContext.loop = false
      this.innerAudioContext.volume = 1.0
      
      this.innerAudioContext.onPlay(() => {
        console.log('Èü≥È¢ëÂºÄÂßãÊí≠Êîæ')
        this.isPlaying = true
      })
      
      this.innerAudioContext.onPause(() => {
        console.log('Èü≥È¢ëÊöÇÂÅú')
        this.isPlaying = false
      })
      
      this.innerAudioContext.onStop(() => {
        console.log('Èü≥È¢ëÂÅúÊ≠¢')
        this.isPlaying = false
      })
      
      this.innerAudioContext.onEnded(() => {
        console.log('Êí≠ÊîæÂÆåÊàê')
        this.isPlaying = false
      })
      
      this.innerAudioContext.onError((err) => {
        console.error('Êí≠ÊîæÂ§±Ë¥•:', err)
        uni.showToast({
          title: 'Êí≠ÊîæÂ§±Ë¥•',
          icon: 'none'
        })
        this.isPlaying = false
      })
    },

    // ÂºÄÂßãÂΩïÈü≥
    async handleStartRecord() {
      console.log('ÁÇπÂáªÂºÄÂßãÂΩïÈü≥')
      this.isStarting = true

      try {
        // ÈáçÁΩÆÁä∂ÊÄÅ
        this.recordTime = 0
        this.waveformPoints = []
        this.currentVolume = 0
        this.hasRecord = false
        this.filePath = ''
        
        // ÁªòÂà∂ÂàùÂßãËÉåÊôØ
        this.drawBackground()

        await this.recordManager.startRecord()
        this.isRecording = true

        uni.showToast({
          title: 'ÂºÄÂßãÂΩïÈü≥',
          icon: 'none',
          duration: 1500
        })
      } catch (error) {
        console.error('ÂºÄÂßãÂΩïÈü≥Â§±Ë¥•:', error)
        uni.showToast({
          title: 'ÂºÄÂßãÂΩïÈü≥Â§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.isStarting = false
      }
    },

    // ÂÅúÊ≠¢ÂΩïÈü≥
    async handleStopRecord() {
      console.log('ÁÇπÂáªÂÅúÊ≠¢ÂΩïÈü≥')
      
      try {
        await this.recordManager.stopRecord()
        this.isRecording = false
        this.hasRecord = true
        this.filePath = this.recordManager.getRecordFile()

        uni.showToast({
          title: 'ÂΩïÈü≥ÂÆåÊàê',
          icon: 'success'
        })

        console.log('ÂΩïÈü≥Êñá‰ª∂Ë∑ØÂæÑ:', this.filePath)
      } catch (error) {
        console.error('ÂÅúÊ≠¢ÂΩïÈü≥Â§±Ë¥•:', error)
        uni.showToast({
          title: 'ÂÅúÊ≠¢ÂΩïÈü≥Â§±Ë¥•',
          icon: 'none'
        })
      }
    },

    // ÈáçÊñ∞ÂΩïÈü≥
    handleRedoRecord() {
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅÈáçÊñ∞ÂΩïÈü≥ÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            this.isRecording = false
            this.hasRecord = false
            this.isPlaying = false
            this.recordTime = 0
            this.filePath = ''
            this.waveformPoints = []
            this.currentVolume = 0
            
            // ÈáçÁΩÆÂΩïÈü≥ÁÆ°ÁêÜÂô®
            this.recordManager.tempFilePath = ''
            this.recordManager.recordTime = 0
            
            // ÈáçÊñ∞ÁªòÂà∂ËÉåÊôØ
            this.drawBackground()
          }
        }
      })
    },

    // Êí≠ÊîæÂΩïÈü≥
    handlePlay() {
      console.log('ÁÇπÂáªÊí≠ÊîæÂΩïÈü≥')
      
      if (!this.filePath) {
        uni.showToast({
          title: 'ËØ∑ÂÖàÂΩïÈü≥',
          icon: 'none'
        })
        return
      }

      if (this.isPlaying) {
        // ÊöÇÂÅúÊí≠Êîæ
        this.innerAudioContext.pause()
        this.isPlaying = false
      } else {
        // ÂºÄÂßãÊí≠Êîæ
        this.innerAudioContext.src = this.filePath
        this.innerAudioContext.play()
        this.isPlaying = true
      }
    }
  }
}
</script>

